<template>
  <div class="d-flex">
    <div class="d-flex flex-column" style="flex-grow: 1">
      <div class="d-flex" style="align-items: center">
        <div class="pt-2 pb-2">
          <v-avatar color="blue">
            <img v-if="img" v-bind:src="img" v-bind:alt="texto" />
            <v-icon
              class="white--text headline"
              style="font-size: 3rem !important;"
              v-else
            >mdi-account</v-icon>
          </v-avatar>
        </div>

        <div style="flex-grow: 1">
          <div class="pa-2 d-flex flex-column align-start" style="flex-grow: 1">
            <div>
              <strong>{{texto}}</strong>
            </div>
            <div>{{endereco}}</div>
            <div>
              <small>{{data}}</small>
            </div>
          </div>
        </div>

        <div align="start">
          <div class="pa-2 pb-0">
            <v-icon color="primary">{{ iconVenda }}</v-icon>
            {{totalFormatado}}
          </div>
          <div class="pa-2 pt-0">
            <v-icon color="success">{{ iconDesconto }}</v-icon>
            {{descontoFormatado}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    iconVenda: "mdi-cash",
    iconDesconto: "mdi-coin"
  }),
  props: ["texto", "total", "desconto", "endereco", "data", "img"],
  computed: {
    totalFormatado() {
      const valor = this.total ? this.total : 0;
      return valor.toLocaleString("pt-BR", {
        style: "currency",
        currency: "BRL"
      });
    },
    descontoFormatado() {
      const valor = this.desconto ? this.desconto : 0;
      return valor.toLocaleString("pt-BR", {
        style: "currency",
        currency: "BRL"
      });
    }
  }
};
</script>