<template>
    <div class="d-flex">
      <div class="d-flex" style="flex-grow: 1" outlined tile>
        <div>
          <v-avatar v-bind:color="texto.length > 3 ? '#be0b43' : 'blue'" v-bind:size="texto.length > 3 ? '65' : '55'">
            <span class="white--text" style="font-size: 1.2em">{{texto}}</span>
          </v-avatar>
        </div>
      </div>
      <div align="start">
        <div outlined tile>
            <v-icon color="primary">{{ iconVenda }}</v-icon> 
            <span>{{totalFormatado}}</span>
        </div>
        <div outlined tile>
            <v-icon color="success">{{ iconDesconto }}</v-icon> 
            <span>{{descontoFormatado}}</span>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data: () => ({
    iconVenda: 'mdi-cash',
    iconDesconto: 'mdi-coin'
  }),
  props: ['texto', 'total', 'desconto'],
  computed: {
    totalFormatado() {
      const valor = this.total ? this.total : 0;
      return valor.toLocaleString("pt-BR", {
        style: "currency",
        currency: "BRL"
      });
    },
    descontoFormatado() {
      const valor = this.desconto ? this.desconto : 0;
      return valor.toLocaleString("pt-BR", {
        style: "currency",
        currency: "BRL"
      });
    }
  }
};
</script>